.mod-builder-sect {
  height: 100%;
}

.nav-wrapper {
  height: 5%;
}

.sect-container {
  display: flex;
  flex-direction: column;

  height: 100%;
}

.sect-main {
  display: flex;
  flex-direction: row;

  height: 100%;
}

.sect-file-explorer {
  overflow: hidden;

  width: 25%;
  height: 100%;

  border-right: 1px solid white;
}

.sect-file-explorer-list {
  resize: none;
  overflow-y: scroll;
  overflow-x: hidden;

  background-color: rgba(0, 0, 0, 0);
  color: white;

  width: 100%;
  height: 100%;

  margin: 0;
  padding: 0;
}

.sect-text-area {
  display: flex;
  flex-direction: column;

  padding: 5px;

  width: 75%;
  height: 100%;
}

.sect-text-area > textarea {
  flex: 1;
  resize: none;
  overflow: auto;

  background-color: rgba(0, 0, 0, 0);
  border: none;
  color: white;

  width: 100%;

  padding: 0;
  margin: 0;

  white-space: nowrap;
}

.sect-text-area > textarea:focus {
  outline: none;
}

.file-explorer-file-label {
  font-style: italic;
}

.file-explorer-dir-label {
  text-decoration: underline;
}

.file-explorer-list {
  list-style: none;
  padding-left: 8px;
}

.file-explorer-list-item {
  color: white;
  user-select: none;
}

.file-explorer-list-item:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.file-explorer-list-item > span {
  cursor: pointer;
}

.file-explorer-row > .file-explorer-options {
  visibility: hidden;
}

.file-explorer-row:hover > .file-explorer-options {
  visibility: visible;
}

.text-area-toolbar, .file-explorer-toolbar {
  display: flex;
  flex-direction: row;

  padding: 3px;

  justify-content: flex-end;

  height: 20px;
}

.text-area-toolbar > label, .file-explorer-toolbar > label {
  color: white;
  margin-right: 5px;
}

.text-area-toolbar {
  background-color: rgba(0, 0, 0, 0.2);
}

.file-explorer-toolbar {
  background-color: rgba(255, 255, 255, 0.05);
  padding-right: 23px;
}

.editor-toolbar-icon {
  color: grey;
}

.editor-toolbar-icon:not(:first-child) {
  margin-left: 10px;
}

.save-active:hover {
  color: aqua;
}

.icon-active, .dir-item-icon:hover {
  color: white;
  cursor: pointer;
}

.file-explorer-options {
  display: flex;
  flex-direction: row;

  padding-right: 5px;

  justify-content: space-between;
}

.dir-item {
  display: flex;
  flex-direction: row;

  justify-content: space-between;
}

.file-item {
  display: flex;
  flex-direction: row;

  justify-content: space-between;
}